import React, { useRef, Suspense } from 'react'
import { Canvas, useFrame } from 'react-three-fiber'
import { useGLTF, OrbitControls, Loader } from '@react-three/drei'
import Text from './Text'
import './styles.css'


import Hud from './components/Hud'
import Main from './components/Main'

function Jumbo(props) {
  const ref1 = useRef()
  const ref2 = useRef()
  const ref3 = useRef()
  const ref4 = useRef()
  const ref5 = useRef()
  const ref6 = useRef()
  const ref7 = useRef()
  useFrame(({ clock }) => (ref1.current.rotation.x  = Math.sin(clock.getElapsedTime()) * 0.03))
  useFrame(({ clock }) => (ref2.current.rotation.x  = Math.cos(clock.getElapsedTime()) * 0.01))
  useFrame(({ clock }) => (ref3.current.rotation.x  = Math.sin(clock.getElapsedTime()) * 0.04))
  useFrame(({ clock }) => (ref4.current.rotation.x  = Math.cos(clock.getElapsedTime()) * 0.08))
  useFrame(({ clock }) => (ref5.current.rotation.x  = Math.sin(clock.getElapsedTime()) * 0.01))
  useFrame(({ clock }) => (ref6.current.rotation.x  = Math.cos(clock.getElapsedTime()) * 0.03))
  useFrame(({ clock }) => (ref7.current.rotation.x  = Math.sin(clock.getElapsedTime()) * 0.06))
  
  return (
    <>
    <group  ref={ref1}  rotation-y={Math.PI * 2.5}  >
      <Text hAlign="right"  position={[-3+0.7, 2, -3]} children="F" />
    </group>
    <group  ref={ref2}  rotation-y={Math.PI * 2.5}  >
    <Text hAlign="right"  position={[-2.5+0.7, 2, -3]} children="L" />
    </group>
    <group  ref={ref3}  rotation-y={Math.PI * 2.5}  >
    <Text hAlign="right"  position={[-2+0.7, 2, -3]} children="O"  />
    </group>
    <group  ref={ref4}  rotation-y={Math.PI * 2.5}  >
    <Text hAlign="right"  position={[-1.4+0.7, 2, -3]} children="A" />
    </group>
    <group  ref={ref5}  rotation-y={Math.PI * 2.5}  >
    <Text hAlign="right"  position={[-0.9+0.7, 2, -3]} children="T" />
    </group>
    <group  ref={ref6}  rotation-y={Math.PI * 2.5}  >
    <Text hAlign="right"  position={[-0.1+0.7, 2, -3]} children="3" size={0.8} />
    </group>
    <group  ref={ref7}  rotation-y={Math.PI * 2.5}  >
    <Text hAlign="right"  position={[0.8+0.7, 2, -3]} children="D" size={0.8} />
    </group>
  </>
  )
}

function Model(props) {
  const group1 = useRef()
  const group2 = useRef()
  const group3 = useRef()

  useFrame(() => (group1.current.rotation.y -= -0.004))
  useFrame(() => (group2.current.rotation.y -= 0.0015))
  useFrame(() => (group3.current.rotation.y -= -0.003))
  
  const { nodes, materials } = useGLTF('earthuni1.glb')
  return (
    <group {...props} dispose={null}>
      <mesh ref={group1} position={[0.5,0,1]}  geometry={nodes.Sphere.geometry} material={materials['Material.001']} />
      <mesh ref={group2} geometry={nodes.Sphere001.geometry} material={materials['Material.003']} scale={5.52} />
      <mesh ref={group3} position={[0.5,0,1]} geometry={nodes.Sphere002.geometry} material={materials['Material.005']} scale={1.02} />
    </group>
  )
}
 


export default function App() {
  return (
    <>
    
    <Canvas
    camera={{ position: [3, 0, 0] }}>
      
      <pointLight position={[0, 3, -5]} intensity={1} />
      <ambientLight intensity={0.05} />
      
      <Suspense fallback={null}>
        <Jumbo />
        <Model />
      </Suspense>
      <OrbitControls enablePan={false} enableZoom={false} maxPolarAngle={Math.PI / 2.5} minPolarAngle={Math.PI / 4}  />
      
    </Canvas>
    <Loader />
    <Hud/>
    <Main/>
    

    
    
    </>
    
  )
}
